import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';

/**
 * 
 */
export class GSPUserListEntity extends Entity {

  /**
   * 
   */
  @NgField(
    {
      primary: true,
      dataField: 'id'
    }
  )
  id: string;

  /**
   * 
   */
  @NgField()
  code: string;

  /**
   * 
   */
  @NgField()
  name: string;

  /**
  * 
  */
  @NgField()
  userType: number;

  /**
   * 
   */
  @NgField()
  sysOrgId: string;
  /**
   * 
   */
  @NgField()
  sysOrgCode: string;
  /**
   * 
   */
  @NgField()
  sysOrgName: string;
  /**
   * 
   */
  @NgField()
  creatorName: string;

    /**
   * 
   */
  @NgField()
  note: string;

}



