import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { GSPUserDetailStateMachine } from '../gspuser-detail.statemachine';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userDetailCancel'
})
export class UserDetailCancelHandler extends CommandHandler {

  constructor(
    private stateMachine: GSPUserDetailStateMachine
  ) {
    super();
  }

  schedule() {
    this.addTask('cancel', () => {
      // TODO:
      console.log('');
    });

    // 
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}




