import { Injectable } from '@angular/core';
import { StateMachine, State, RenderState, NgStatemachine, NgRenderState, NgState } from '@farris/devkit';


@Injectable()
@NgStatemachine()
export class GSPUserListStateMachine extends StateMachine {

  /**
   * 
   */
  @NgState({ initialState: true })
  init: State;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'init'
  })
  canAdd: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'init'
  })
  canEdit: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'init'
  })
  canRemove: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'init'
  })
  canView: RenderState;

}




