import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserPositionService } from '../../domain/gspposition.dataservice';
import { GSPUserDetailUIState } from '../../../app/gspuser-detail.uistate';
import { GSPUserPositionUIState } from '../user-position.uistate';


/**
 * --
 */
@Injectable()
@NgCommandHandler({
    commandName: 'saveUserPos'
})
export class SaveUserPosition extends CommandHandler {

    constructor (
        private dataService: GSPUserPositionService) {
        super();
    }

    schedule () {
        this.addTask('save', () => {
            // 
            this.dataService.modifyPosByUser().subscribe(
                // 
                result => {
                    alert('');
                 },
                error => {
                    console.log(error);
                    console.log('');
                }
            );
        });
    }

}



