import { Component, OnInit, Input } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { UserPasswordDataService } from './domain/userpassword.dataservice';
import { UserPassWord } from './domain/password';

/**
 * 
 */
@Component({
    selector: 'app-user-password',
    templateUrl: './userpassword.component.html'
})
export class UserPasswordComponent implements OnInit {
    authenstrategies: string[];
    curAuthenStrategy: string;
    @Input()
    isView: boolean;
    @Input()
    userId: string;
    constructor (
        private service: UserPasswordDataService,
        public pwd: UserPassWord
    ) {
    }

    ngOnInit () {
        // Tab
        this.pwd.isPwdHasLoad = true;
        this.authenstrategies = [''];
        this.curAuthenStrategy = this.authenstrategies[0];
        this.service.formload(this.userId);
    }
}







