import { SysManagerUtil } from './../../../common/util/sys-manager-util';
import { Injectable, Injector } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { GSPUserListDataStorage } from './gspuser-list.datastorage';
import { Observable } from 'rxjs/Observable';


/**
 * 
 */
@Injectable()
export class GSPUserListDataService {

  /**
   * 
   */
  constructor (
    private rest: RestfulService,
    private dataStorage: GSPUserListDataStorage,
    private userSession: SysManagerUtil
  ) { }

  /**
   * 
   */
  getAllUsers (): void {
    const allUsersUrl = `${ this.userSession.userWebApi }/all`;
    const headers = this.userSession.getHeader();
    this.rest.get(allUsersUrl, null, headers).subscribe(
      (result: any) => {
        this.dataStorage.load(result);
      }, error => {
        console.log(error);
        console.log('');
      }
    );
  }

  /**
   * 
   * @param codeOrName 
   */
  getUsersByCodeOrName (codeOrName: string) {
    const filter = { 'userCodeOrName': codeOrName };
    this.getUserByFilter(filter, '');
  }

  /**
   * 
   * @param sysOrg 
   */
  getUsersBySysOrg (sysOrg: string) {
    const filter = { 'sysOrgId': sysOrg };
    this.getUserByFilter(filter, '');
  }

  /**
   * 
   * @param filter 
   * @param errorMsg 
   */
  private getUserByFilter (filter: object, errorMsg: string) {
    const userUrl = this.userSession.userWebApi;
    const headers = this.userSession.getHeader();
    this.rest.get(userUrl, filter, headers).subscribe(result => {
      this.dataStorage.load(result);
    }, error => {
      console.log(error);
      console.log(errorMsg);
    });
  }

  /**
   * 
   */
  remove (id: string): Observable<any> {
    const headers = this.userSession.getHeader();
    const deleteUrl = `${ this.userSession.userWebApi }/${ id }`;
    return this.rest.delete(deleteUrl, null, headers);
  }
}




