import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPUserDetailEntity } from './entities/gspuser-detail.entity';
import { GSPUserDetailDataStorage } from './gspuser-detail.datastorage';

/**
 * 
 */
@Injectable()
@NgRepository({
  from: GSPUserDetailDataStorage,
  entityType: GSPUserDetailEntity
})
export class GSPUserDetailRepository extends Repository<GSPUserDetailEntity> {
  constructor(injector: Injector) {
      super(injector);
  }
}




