import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPUserDetailEntity, GSPUserDetailRepository } from '../domain/gspuser-detail-domain.index';

/**
 * 
 */
@Injectable()
@NgBindingData({
  entity:     GSPUserDetailEntity,
  repository: GSPUserDetailRepository
})
export class GSPUserDetailBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}




