import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserPositionUIState } from '../user-position.uistate';
import { GSPUserPositionRepository } from '../../domain/gspposition.repository';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
    commandName: 'addUserPos'
})
export class AddUserPosition extends CommandHandler {

    constructor (
        private userPosUIState: GSPUserPositionUIState,
        private userPosRep: GSPUserPositionRepository) {
        super();
    }

    // TODO: 
    schedule () {
        this.addTask('add', () => {
            // 
            this.userPosUIState.userPosHelpFlag = !this.userPosUIState.userPosHelpFlag;
            const curPosList = this.userPosRep.getAllEntities();
            console.log(curPosList);
        });
    }
}



