
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { GSPUserListDataService } from '../../domain/user-list-domain.index';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userListFormLoad'
})
export class UserListFormLoadHandler extends CommandHandler {

  constructor(private dataService: GSPUserListDataService) {
    super();
  }

  schedule() {
    this.addTask('loadData', () => {
      this.dataService.getAllUsers();
    });
  }
}




