import { Provider } from '@angular/core';
import { GSPUserListBindingData } from './gspuser-list.bindingdata';
import { GSPUserListForm } from './gspuser-list.form';
import { GSPUserListStateMachine } from './gspuser-list.statemachine';
import { GSPUserListUIState } from './gspuser-list.uistate';
import { GSPUserListViewModel } from './gspuser-list.viewmodel';


// Handlers
import { UserListFormLoadHandler } from './handlers/user-list-form-load.handler';
import { UserListSearchHandler } from './handlers/user-list-search.handler';
import { UserListRouteHandler } from './handlers/user-list-route.handler';
import { UserListRemoveHandler } from './handlers/user-list-remove.handler';

import { UserDetailFormLoadHandler } from '../../gspuser-detail/app/handlers/user-detail-form-load.handler';
import { UserDetailSaveHandler } from '../../gspuser-detail/app/handlers/user-detail-save.handler';
import { UserDetailEditHandler } from '../../gspuser-detail/app/handlers/user-detail-edit.handler';
import { UserDetailCloseHandler } from '../../gspuser-detail/app/handlers/user-detail-close.handler';
import { UserDetailAddHandler } from '../../gspuser-detail/app/handlers/user-detail-add.handler';
import { UserDetailCancelHandler } from '../../gspuser-detail/app/handlers/user-detail-cancel.handler';
import { UserPositionFormLoadHandler, DeleteUserPosition, SaveUserPosition, AddUserPosition } from '../../gspuser-detail/user-position/app';

// -
const userListCommandHandlers = [
  UserListFormLoadHandler,
  UserListSearchHandler,
  UserListRouteHandler,
  UserListRemoveHandler
];

/** */
export const allCommandHandlers = [
  // 
  UserListFormLoadHandler,
  UserListSearchHandler,
  UserListRouteHandler,
  UserListRemoveHandler,
  // 
  UserDetailFormLoadHandler,
  UserDetailSaveHandler,
  UserDetailCloseHandler,
  UserDetailEditHandler,
  UserDetailAddHandler,
  UserDetailCancelHandler,
  // -
  UserPositionFormLoadHandler,
  AddUserPosition,
  DeleteUserPosition,
  SaveUserPosition
];



// 
const USERLIST_APP_PROVIDERS: Provider[] = [
  GSPUserListBindingData,
  GSPUserListForm,
  GSPUserListStateMachine,
  GSPUserListUIState,
  GSPUserListViewModel
];

export {
  USERLIST_APP_PROVIDERS,
  userListCommandHandlers,
  GSPUserListBindingData,
  GSPUserListForm,
  GSPUserListStateMachine,
  GSPUserListUIState,
  GSPUserListViewModel
};



