import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { UserPassWord } from './password';
import { SysManagerUtil } from '../../../../common/util/sys-manager-util';


@Injectable()
export class UserPasswordDataService {
    private url = '/api/runtime/sys/v1.0/authenitems';
    private accounturl = '/api/runtime/sys/v1.0/accounts';
    UserPassWord: string;
    EnsurePwd: string;
    oldPwd: string;
    constructor (
        private rest: RestfulService,
        private userManagerSession: SysManagerUtil,
        private pwd: UserPassWord
    ) {
    }

    // 
    formload (userId: string) {
        if (userId === 'new') {
            return;
        }
        this.rest.get(this.url + '/' + userId, null, this.userManagerSession.getHeader()).subscribe((result: any) => {
            console.log(result);
            this.pwd.oldPwd = result.password;
            this.pwd.newPwd = result.password;
            this.pwd.enSurePwd = result.password;
            this.pwd.pwdInfo = result;
        });
    }

    // 
    add (userId: string) {
        const autheninfo = {
            'Id': userId,
            'UserId': userId,
            'AccountId': userId,
            'Password': this.pwd.newPwd,
            'TenantId': 10000,
            'ValidTime': '9999',
            'IsNextModifyPassword': 0
        };
        const accountinfo = {
            'Id': userId,
            'UserId': userId,
            'AccountId': userId,
            'TenantId': 10000,
            'AuthenStrategyName': '',
            'AuthenStrategyId': '1'
        };
        this.rest.post(this.accounturl, accountinfo, null, this.userManagerSession.getHeader())
            .subscribe((addResult: any) => {
                this.rest.post(this.url, autheninfo, null, this.userManagerSession.getHeader())
                    .subscribe((result: any) => {
                        // 
                        this.pwd.oldPwd = this.pwd.newPwd;
                    },
                        error => {
                            console.log(error);
                        });
            },
                error => {
                    console.log(error);
                });

    }

    // 
    edit (userId: string) {
        this.pwd.pwdInfo['password'] = this.pwd.newPwd;
        this.rest.put(this.url, this.pwd.pwdInfo, null, this.userManagerSession.getHeader())
            .subscribe(() => {
                this.pwd.oldPwd = this.pwd.newPwd;
        }, error => {
            console.log(error);
            });
        return true;
    }

    /** */
    clearPwd () {
        this.pwd.oldPwd = '';
        this.pwd.newPwd = '';
        this.pwd.enSurePwd = '';
        this.pwd.isPwdHasLoad = false;
    }

    /** */
    checkPwdForAdd (): string {
        if (this.pwd.newPwd === undefined || this.pwd.newPwd === '' || this.pwd.enSurePwd === '') {
            return '';
        }
        if (this.pwd.newPwd !== this.pwd.enSurePwd) {
            return '';
        }
        if (this.pwd.newPwd.length < 6) {
            return '';
        }
        return '';
    }

    /** */
    checkPwdForEdit (): string {
        // 
        if (this.pwd.isPwdHasLoad === false) {
            return 'unchanged';
        } else {
            if (this.pwd.oldPwd === this.pwd.newPwd && this.pwd.oldPwd === this.pwd.enSurePwd) {
                return 'unchanged';
            }
        }
        return this.checkPwdForAdd();
    }
}



