import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPUserListEntity, GSPUserListRepository } from '../domain/user-list-domain.index';

/**
 * 
 */
@Injectable()
@NgBindingData({
  entity:     GSPUserListEntity,
  repository: GSPUserListRepository
})
export class GSPUserListBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}




