import { GSPPositionBindingData } from './user-position.bindingdata';
import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel, NgCommand } from '@farris/devkit';
import { GSPUserPositionUIState } from './user-position.uistate';


/**
 * 
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: GSPPositionBindingData
})
export class GSPUserPositionViewModel extends ViewModel {

  public uiState: GSPUserPositionUIState;

  /**
   * 
   */
  constructor (injector: Injector) {
    super(injector);
    this.uiState = this.injector.get<GSPUserPositionUIState>(GSPUserPositionUIState);
  }

  /**
   * 
   */
  @NgCommand({
    name: 'userPosFormLoad'
  })
  public formLoad () { }

  /** */
  @NgCommand({
    name: 'addUserPos'
  })
  public addUserPos () { }

  /** */
  @NgCommand({
    name: 'deleteUserPos'
  })
  public deleteUserPos () { }

  /** */
  @NgCommand({
    name: 'saveUserPos'
  })
  public saveUserPos () { }

}



