import { GSPUserDetailDataStorage } from './../../domain/gspuser-detail.datastorage';
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import {
  GSPUserDetailEntity, GSPUserDetailDataService, GSPUserDetailRepository
} from '../../domain/gspuser-detail-domain.index';
import { GSPUserDetailStateMachine } from '../gspuser-detail.statemachine';
import { UserPasswordDataService } from '../../userpassword/domain/userpassword.dataservice';
import { GSPUserDetailUIState } from '../gspuser-detail.uistate';
import { GSPUserPositionService } from '../../user-position/domain';
import { HttpErrorResponse } from '@angular/common/http';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
    commandName: 'userDetailSave',
})
export class UserDetailSaveHandler extends CommandHandler {

  constructor (
    private userDetailService: GSPUserDetailDataService,
    private userDetailRep: GSPUserDetailRepository,
    private userDetailStore: GSPUserDetailDataStorage,
    private stateMachine: GSPUserDetailStateMachine,
    private pwdService: UserPasswordDataService,
    private userDetailUIState: GSPUserDetailUIState,
    private userPosService: GSPUserPositionService
  ) {
    super();
  }

  schedule () {
    this.addTask('save', () => {
      const entities = this.userDetailRep.getAllEntities();
      const newUser = entities[0];
      // newUser.sysOrgId = '001';
      newUser.userGroup = 'admin';
      console.log(newUser.data);
      if (this.stateMachine.context.state === 'addState') {
        this.saveAddUser(newUser);
      } else {
        this.saveEditUser(newUser);
      }
    });
  }

  saveAddUser (user: GSPUserDetailEntity) {
    const pwdErrorMsg = this.pwdService.checkPwdForAdd();
    if (pwdErrorMsg !== '') {
      alert(pwdErrorMsg);
      return;
    }
    this.userDetailService.post(user.data)
      .subscribe(
        userId => {
          // new
          this.userDetailUIState.dataId = userId;
          // 
          this.userDetailService.getUserById(userId)
            .subscribe(result =>
              this.userDetailStore.load(result));
          // 
          this.pwdService.add(userId);
          // 
          if (this.userDetailUIState.needSaveUserPos) {
            this.userPosService.modifyPosByUser().subscribe(() => { });
          }
          // 
          this.stateMachine['edit']();
          alert('!');
        },
        (error: HttpErrorResponse) => {
          console.log(error);
          alert(error.error.Message);
        });
  }

  saveEditUser (user: GSPUserDetailEntity) {
    const pwdErrorMsg = this.pwdService.checkPwdForEdit();
    if (pwdErrorMsg !== '' && pwdErrorMsg !== 'unchanged') {
      alert(pwdErrorMsg);
      return;
    }
    this.userDetailService.put(user.data)
      .subscribe(() => {
        // 
        if (pwdErrorMsg === 'unchanged') {
        } else {
          // 
          this.pwdService.edit(user.id);
        }
        // 
        if (this.userDetailUIState.needSaveUserPos) {
          this.userPosService.modifyPosByUser().subscribe(() => { });
        }
        this.stateMachine['edit']();
        alert('!');
      },
      (error: HttpErrorResponse) => {
          console.log(error);
          alert(error.error.Message);
        });
  }
}



