import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import { SysManagerUtil } from '../../../common/util/sys-manager-util';


/**
 * restful
 */
@Injectable()
export class GSPUserDetailDataService {

  constructor (
    private rest: RestfulService,
    private userManagerSession: SysManagerUtil
  ) {
  }

  /**
   * 
   * @param dataId 
   */
  getUserById (dataId: string): Observable<any> {
    const headers = this.userManagerSession.getHeader();
    return this.rest.get(`${ this.userManagerSession.userWebApi }/${ dataId }`, null, headers);
  }

  /** */
  getNewEmptyUser (): Observable<any> {
    // 
    return this.getUserById('new');
  }

  /**
   * 
   */
  post (data: any): Observable<any> {
    const postUrl = this.userManagerSession.userWebApi;
    return this.rest.post(postUrl, data, null, this.userManagerSession.getHeader());
  }
  /**
   * 
   */
  put (data: any): Observable<any> {
    const putUrl = this.userManagerSession.userWebApi;
    return this.rest.put(putUrl, data, null, this.userManagerSession.getHeader());
  }
}



