import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserPositionService } from '../../domain/gspposition.dataservice';
import { GSPUserDetailUIState } from '../../../app/gspuser-detail.uistate';
import { GSPPositionDataStorage } from './../../domain/gspposition.datastorage';
import { GSPUserPositionUIState } from '../user-position.uistate';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/from';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
    commandName: 'userPosFormLoad'
})
export class UserPositionFormLoadHandler extends CommandHandler {

    constructor (
        private dataService: GSPUserPositionService,
        private posStore: GSPPositionDataStorage,
        private userDetailState: GSPUserDetailUIState,
        private userPosState: GSPUserPositionUIState) {
        super();
    }

    schedule () {
        this.addTask('loadData', () => {
            // 
            const dataId = this.userDetailState.dataId;
            this.dataService.getPositionsByUserId(dataId).subscribe((result: any) => {
                this.posStore.load(result);
                // ,
                this.userPosState.posIdBackUpList = [];
                // 
                this.userPosState.curPosIdList = [];
                Observable.from(result)
                    .map(item => item['id'])
                    .subscribe(item => {
                        this.userPosState.posIdBackUpList.push(item);
                        this.userPosState.curPosIdList.push(item);
                    });
            });
        });
    }
}



