import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { GSPUserListDataService, GSPUserListRepository } from '../../domain/user-list-domain.index';
import { GSPUserListUIState } from '../gspuser-list.uistate';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userListRemove'
})
export class UserListRemoveHandler extends CommandHandler {

  // UI
  constructor(
    private dataService: GSPUserListDataService,
    private uiState: GSPUserListUIState,
    private userListRep: GSPUserListRepository
  ) {
    super();
  }

  schedule() {

    // 
    this.addTask('remove', () => {
      if (this.uiState.selectedIds.length === 0) {
        alert('');
        return;
      }
      const deleteId = this.uiState.selectedIds[0];
      this.dataService.remove(deleteId).subscribe((result: any) => {
        // 
        this.userListRep.removeEntityById(deleteId);
      }, error => {
        console.log(error);
        console.log('');
      });
    });

  }
}



