import { SysManagerUtil } from './../../../common/util/sys-manager-util';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import { GSPUserListDataService } from '../../gspuser-list/domain/gspuser-list.dataservice';
import { GSPSysOrgDataStorage } from './sys-org.datastorage';

/**
 * restful
 */
@Injectable()
export class GSPSysOrgDataService {

    constructor (
        private rest: RestfulService,
        private userListService: GSPUserListDataService,
        private userSession: SysManagerUtil,
    ) {
    }

    /**
     * 
     * @param dataId 
     */
    getUsersWhenOrgChanged (dataId: string) {
        if (dataId !== '') {
            this.userListService.getUsersBySysOrg(dataId);
        } else {
            // 
            this.userListService.getAllUsers();
        }
    }

    /**
     * 
     */
    getAllSysOrgs (): Observable<any> {
        const allSysOrgsUrl = `${ this.userSession.sysOrgWebApi }/all`;
        const headers = this.userSession.getHeader();
        return this.rest.get(allSysOrgsUrl, null, headers).map((result: any[]) => {
            const data = result.map(sysOrg => {
                if (sysOrg.layer === 1 && sysOrg.parentId === '') {
                    sysOrg.parentId = undefined;
                }
                return sysOrg;
            });
            return data;
        });
    }
}



