import { SelectableSettings } from '@progress/kendo-angular-grid';
import { GSPUserPositionViewModel } from './app/user-position.viewmodel';
import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { GSPUserPositionRepository } from './domain';
import { Observable } from 'rxjs/Observable';
import { GSPUserDetailViewModel } from '../app/gspuser-detail.viewmodel';
import { GSPPositionEntity } from './domain/gspposition.entity';

/**
 * 
 */
@Component({
  selector: 'app-user-position',
  templateUrl: './user-position.component.html',
  styleUrls: ['./user-position.component.css'],
})
export class UserPositionComponent implements OnInit, OnChanges {

  /** */
  @Input()
  curUserId: string;

  selectableSettings: SelectableSettings;

  constructor (
    public userPosVm: GSPUserPositionViewModel,
    public userDetailVm: GSPUserDetailViewModel,
    private posRep: GSPUserPositionRepository) {
    // 
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'single'
    };
  }
  ngOnInit () {
  }

  /** */
  ngOnChanges (changes: SimpleChanges): void {
    // 
    this.userPosVm.uiState.selectPosList = [];
    this.userPosVm.uiState.curUserId = this.curUserId;
    this.userPosVm.formLoad();
  }

  /** */
  afterPosSelected (args: any[]) {
    // 
    const newPosEntityList = [];
    this.userPosVm.uiState.curPosIdList = [];
    Observable.from(args).subscribe(
      item => {
        newPosEntityList.push(new GSPPositionEntity(item));
        this.userPosVm.uiState.curPosIdList.push(item['id']);
      }
    );
    this.posRep.clear();
    this.posRep.addEntities(newPosEntityList);

  }
}



