import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';
import { GSPUserListBindingData } from './gspuser-list.bindingdata';
import { GSPUserListForm } from './gspuser-list.form';
import { GSPUserListStateMachine } from './gspuser-list.statemachine';
import { GSPUserListUIState } from './gspuser-list.uistate';

/**
 * 
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: GSPUserListBindingData,
  form: GSPUserListForm,
  stateMachine: GSPUserListStateMachine,
})
export class GSPUserListViewModel extends ViewModel {

  /**
   * UI
   */
  public uiState: GSPUserListUIState;
  public stateMachine: GSPUserListStateMachine;
  public form: GSPUserListForm;

  /**
   * --UI
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get<GSPUserListUIState>(GSPUserListUIState);
  }

  /**
   * 
   */
  @NgCommand({
    name: 'userListFormLoad'
   })
  public formLoad() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userListSearch'
   })
  public search() {}

  /**
   * --routeactionadd
   */
  @NgCommand({
    name: 'userListRoute',
    params: {
      action: 'add'
    }
  })
  public add() {}

  /**
   * --routeactionedit
   */
  @NgCommand({
    name: 'userListRoute',
    params: {
      action: 'edit'
    }
  })
  public edit() {}

  /**
   * --routeactionview
   */
  @NgCommand({
    name: 'userListRoute',
    params: {
      action: 'view'
    }
  })
  public view() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userListRemove',
  })
  public remove() {}

}



