import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { GSPSysOrgDataService } from '../../sys-org-tree/domain';


@Component({
  selector: 'app-sys-org-help',
  templateUrl: './sys-org-help.component.html',
  styleUrls: ['./sys-org-help.component.css']
})
export class SysOrgHelpComponent implements OnInit, OnChanges {

  /** */
  @Input()
  title = '';
  /** */
  @Input()
  isShow: boolean;
  /** */
  @Input()
  initSelectedKeys: string[];

  /** */
  @Output()
  selectedAfterOk: EventEmitter<any> = new EventEmitter<any>();

  /** */
  curSelectedKeys: string[];
  selectableSettings: SelectableSettings;
  data: Object[];

  constructor (private sysOrgService: GSPSysOrgDataService) {
    // 
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'single',
      enabled: true
    };
  }

  ngOnInit () {
  }

  /** */
  ngOnChanges (changes: SimpleChanges): void {
    // 
    if (changes['isShow']) {
      // ,
      if (changes.isShow.firstChange) {
        // 
        this.isShow = false;
        return;
      }
      // 
      this.isShow = true;
      // 
      this.sysOrgService.getAllSysOrgs()
        .subscribe(result => {
          this.data = result;
          this.curSelectedKeys = [];
          if (this.initSelectedKeys) {
            this.initSelectedKeys.map(item => this.curSelectedKeys.push(item));
          }
        }, error => {
          console.log('');
          console.log(error);
        });
    }
  }
  /** */
  btnOk () {
    this.closeDialog();
    // 
    if (this.curSelectedKeys === null || this.curSelectedKeys.length === 0) {
      // 
      return;
    }
    const sysOrg = this.data.find(pos => pos['id'] === this.curSelectedKeys[0]);
    // 
    this.selectedAfterOk.next(sysOrg);
  }
  showDialog () {
    this.isShow = true;
  }
  closeDialog () {
    this.isShow = false;
  }
}



