import { GSPUserListUIState } from '../gspuser-list.uistate';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { SysManagerUtil } from '../../../../common/util/sys-manager-util';

/**
 * -
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userListRoute'
})
export class UserListRouteHandler extends CommandHandler {

  constructor (
    private router: Router,
    private uistate: GSPUserListUIState,
    private session: SysManagerUtil,

  ) {
    super();
  }

  schedule() {
    this.addTask('route', (lastResult: any, context: TaskExecutionContext) => {
      const command = context.command;
      const action = command.params.action;
      // 
      const selectId = this.uistate.selectedIds[0];
      // new;id
      const curUserId = (action === 'add') ? 'new' : selectId;
      if (action === 'view' || action === 'edit') {
        if (curUserId == null || curUserId.length === 0) {
          alert('');
          return;
         }
       }
      // ,
      if (action === 'add') {
        this.session.isView = false;
       }
      const userRouter = this.session.getUserManageRouter();
      this.router.navigate([userRouter, curUserId, { action }]);
    });
  }
}




