import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPUserListEntity } from './entities/gspuser-list.entity';
import { GSPUserListDataStorage } from './gspuser-list.datastorage';

/**
 * 
 */
@Injectable()
@NgRepository({
  from: GSPUserListDataStorage,
  entityType: GSPUserListEntity
})
export class GSPUserListRepository extends Repository<GSPUserListEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}




