import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, NgFormControl, Form } from '@farris/devkit';

@Injectable()
@NgForm({
  bindingData: null
})
export class GSPUserListForm extends Form {

  /**
   * 
   * @param injector 
   */
  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * 
   */
  @NgFormControl({
    name: 'userSearch',
  })
  userSearch: FormControl;

}



