import { NgModule, Provider } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';
import { GSPUserListComponent } from './gspuser-list/gspuser-list.component';
import { GSPUserDetailComponent } from './gspuser-detail/gspuser-detail.component';


const routes: Routes = [
  /** */
  { path: 'users', component: GSPUserListComponent },
  /** */
  { path: 'users/:id', component: GSPUserDetailComponent },
  { path: '', redirectTo: 'users', pathMatch: 'full' },
  // { path: '/:ssid', component: GSPUserListComponent },
  { path: '**', redirectTo: 'users' }
];
/**
 * 
 */
/*
@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class GSPUserManagerRoutingModule {}
*/

export const GSPUserManagerRoutingModule = RouterModule.forChild(routes);





