import { SysManagerUtil } from './../../../../common/util/sys-manager-util';
import { Injectable } from '@angular/core';
import { RestfulService } from '@farris/devkit';
import { Observable } from 'rxjs/Observable';
import { ResultPackage } from '../../../../common';
import { GSPUserPositionUIState } from '../app/user-position.uistate';
import { GSPUserPositionRepository } from './gspposition.repository';


/** */
@Injectable()
export class GSPUserPositionService {

    constructor (
        private rest: RestfulService,
        private sessionUtil: SysManagerUtil,
        private userPosState: GSPUserPositionUIState,
        private userPosRep: GSPUserPositionRepository
    ) { }


    /** */
    getPositionsByUserId (userId: string): Observable<any[]> {
        const headers = this.sessionUtil.getHeader();
        const getPosByUserUrl = this.sessionUtil.userPositionWebApi + '/users/' + userId + '/positions';
        return this.rest.get(getPosByUserUrl, null, headers);
    }

    /** */
    modifyPosByUser (): Observable<any> {
        // 
        const userId = this.userPosState.curUserId;
        const curPosIdList = this.userPosRep.getAllEntities().map(item => item.id);
        const posIdBackUpList = this.userPosState.posIdBackUpList;
        // 
        const addList = curPosIdList.filter(item => posIdBackUpList.indexOf(item) < 0);
        const removeList = posIdBackUpList.filter(item => curPosIdList.indexOf(item) < 0);
        const userPosPkg = new ResultPackage();
        userPosPkg.ownerId = userId;
        userPosPkg.addingResults = addList;
        userPosPkg.removingResults = removeList;
        if (addList.length === 0 && removeList.length === 0) {
            // 
            return new Observable(observer => observer.next());
        }
        console.log('add');
        console.log(addList);
        console.log('remove');
        console.log(removeList);
        const headers = this.sessionUtil.getHeader();
        const userPosWebUrl = this.sessionUtil.userPositionWebApi;
        const queryParams = {
            subjectType: 'user',
            objectType: 'position'
        };
        return this.rest.post(userPosWebUrl, userPosPkg, queryParams, headers)
            .map(item => {
                this.userPosState.posIdBackUpList = curPosIdList;
                return item;
            });
    }

    /** */
    getAllPositions (): Observable<any> {
        const headers = this.sessionUtil.getHeader();
        const posUrl = this.sessionUtil.positionWebApi + '/all';
        return this.rest.get(posUrl, null, headers);
    }
}



