import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserDetailStateMachine } from '../gspuser-detail.statemachine';
import { SysManagerUtil } from '../../../../common/util/sys-manager-util';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userDetailEdit'
})
export class UserDetailEditHandler extends CommandHandler {

  constructor (
    private stateMachine: GSPUserDetailStateMachine,
    private userSession: SysManagerUtil
  ) {
    super();
  }

  schedule() {
    this.addTask('transitState', () => {
      this.stateMachine['edit']();
      this.userSession.isView = false;
    });
  }
}



