import { GSPUserDetailRepository } from './domain/gspuser-detail.repository';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { GSPUserDetailViewModel } from './app/gspuser-detail-app.index';
import { SysManagerUtil } from '../../common/util/sys-manager-util';
import { UserPositionComponent } from './user-position/user-position.component';

@Component({
  selector: 'app-gspuser-detail',
  templateUrl: './gspuser-detail.component.html',

})
export class GSPUserDetailComponent implements OnInit {

  // 
  @ViewChild(UserPositionComponent)
  private userPosChild: UserPositionComponent;

  // ActivatedRoute
  constructor (
    public viewModel: GSPUserDetailViewModel,
    private userDetailRep: GSPUserDetailRepository,
    private route: ActivatedRoute,
    public userSession: SysManagerUtil
  ) {
  }

  ngOnInit () {

    this.route.paramMap.subscribe((params: Params) => {
      // 
      this.viewModel.uiState.dataId = params.get('id');
      this.userSession.curUserId = params.get('id');
      const curAction = params.get('action');
      this.viewModel.uiState.action = curAction;
      // UI
      if (curAction === 'view') {
        this.userSession.isView = true;
      } else {
        this.userSession.isView = false;
       }
      this.viewModel.formLoad();
    });
  }

  /** */
  saveUser () {
    // ,
    this.viewModel.uiState.needSaveUserPos = false;
    // ,
    if (this.userPosChild) {
      this.viewModel.uiState.needSaveUserPos = true;
    }
    this.viewModel.save();
  }

  /** */
  showSysOrgHelp () {
    // 
    this.viewModel.uiState.showSysOrgHelpFlag = !this.viewModel.uiState.showSysOrgHelpFlag;
  }

  /** */
  afterSelectedSysOrg (data: any) {
    const curUserDetail = this.userDetailRep.getAllEntities()[0];
    curUserDetail.sysOrgId = data.id;
    curUserDetail.sysOrgName = data.name;
   }
}



