import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { GSPPositionDataStorage } from './gspposition.datastorage';
import { GSPPositionEntity } from './gspposition.entity';

/**
 * 
 */
@Injectable()
@NgRepository({
  from: GSPPositionDataStorage,
  entityType: GSPPositionEntity
})
export class GSPUserPositionRepository extends Repository<GSPPositionEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}




