import { GSPUserDetailDataStorage } from './../../domain/gspuser-detail.datastorage';

import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserDetailDataService } from '../../domain/gspuser-detail-domain.index';
import { GSPUserDetailStateMachine } from '../gspuser-detail.statemachine';
import { GSPUserDetailUIState } from '../gspuser-detail.uistate';


/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userDetailFormLoad'
})
export class UserDetailFormLoadHandler extends CommandHandler {

  constructor(
    private userDetailService: GSPUserDetailDataService,
    private userDetailStore: GSPUserDetailDataStorage,
    private uiState: GSPUserDetailUIState,
    private stateMachine: GSPUserDetailStateMachine
  ) {
    super();
  }

  /** */
  schedule() {
    this.addTask('loadData', () => {
      // 
      const dataId = this.uiState.dataId;
      // 
      if (dataId === 'new') {
        this.userDetailService.getNewEmptyUser()
          .subscribe(
          result => {
            this.userDetailStore.load(result);
            this.uiState.dataId = result[0]['id'];
          });
      } else {
        this.userDetailService.getUserById(dataId)
          .subscribe((result: any) => {
          // load,
            if (result[0]['id'] === undefined || result[0]['id'] === null || result[0]['id'] === '') {
              alert(`id=${ dataId }`);
              return;
            }
            this.userDetailStore.load(result);
            console.log(result);
        });
      }
    });

    // 
    this.addTask('transitState', () => {
      const action = this.uiState.action;
      this.stateMachine[action]();
    });
  }
}



