import { Injectable } from '@angular/core';
import {
  StateMachine, NgStatemachine, State, NgState,
  RenderState, NgRenderState, Action, NgAction
} from '@farris/devkit';

@Injectable()
@NgStatemachine()
export class GSPUserDetailStateMachine extends StateMachine {

  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */

  /**
   * 
   */
  @NgState({ initialState: true })
  viewState: State;

  /**
   * 
   */
  @NgState()
  addState: State;

  /**
   * 
   */
  @NgState()
  editState: State;


  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAdd: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canEdit: RenderState;


  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canCancel: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canSave: RenderState;

  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canInput: RenderState;

    /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'editState'
  })
  canStrictEdit: RenderState;

  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */
  @NgAction({transitTo: 'addState' })
  add: Action;

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'viewState' })
  view: Action;
}




