
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserPositionService } from '../../domain/gspposition.dataservice';
import { GSPUserPositionRepository } from './../../domain/gspposition.repository';
import { GSPUserPositionUIState } from '../user-position.uistate';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
    commandName: 'deleteUserPos'
})
export class DeleteUserPosition extends CommandHandler {

    constructor (
        private dataService: GSPUserPositionService,
        private userPosUIState: GSPUserPositionUIState,
        private userPosRep: GSPUserPositionRepository) {
        super();
    }

    // TODO: 
    schedule () {
        this.addTask('delete', () => {

            // 
            if (this.userPosUIState.selectPosList === null
                || this.userPosUIState.selectPosList.length === 0) {
                alert('');
                return;
            }
            if (this.userPosRep.getAllEntities().length === 0) {
                alert('');
                return;
            }
            const curSelectPosId = this.userPosUIState.selectPosList[0];
            // 
            this.userPosRep.removeEntityById(curSelectPosId);
            // 
            this.userPosUIState.selectPosList = [];
        });
    }
}



