import { Component, OnInit } from '@angular/core';
import { GSPUserListViewModel } from './app/user-list-app.index';
import { SelectableSettings, PageChangeEvent, GridDataResult, PagerSettings} from '@progress/kendo-angular-grid';
import { ActivatedRoute, Params } from '@angular/router';
import { SysManagerUtil } from '../../common/util/sys-manager-util';




@Component({
    selector: 'app-gspuser-list',
    templateUrl: './gspuser-list.component.html',
})
export class GSPUserListComponent implements OnInit {

  userList: any[];
  public pageSetting: PagerSettings;
  public selectableSettings: SelectableSettings;
  constructor (
    public viewModel: GSPUserListViewModel,
    private route: ActivatedRoute,
    private currentSession: SysManagerUtil
  ) {
  }

  ngOnInit () {
    // 
    this.viewModel.form.userSearch.setValue('');
    this.viewModel.uiState.selectedIds = [];
    // funcid
    this.route.paramMap.subscribe((params: Params) => {
      if (params.has('funcid')) {
        this.currentSession.funcId = params.get('funcid');
      }
    });
    this.viewModel.bindingData.changes.subscribe(
      result => {
        this.userList = this.viewModel.bindingData.list.toJSON();
      }
    );
    this.viewModel.formLoad();
    // 
    this.pageSetting = {
      buttonCount: 5,
      info: false,
      pageSizes: true,
      previousNext: true
    };
      // 
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'single',
      enabled: true
    };
  }

}



