import { Provider } from '@angular/core';
import { GSPUserDetailBindingData } from './gspuser-detail.bindingdata';
import { GSPUserDetailForm } from './gspuser-detail.form';
import { GSPUserDetailStateMachine } from './gspuser-detail.statemachine';
import { GSPUserDetailViewModel } from './gspuser-detail.viewmodel';
import {GSPUserDetailUIState } from './gspuser-detail.uistate';
import { userDetailCommandHandlers } from './handlers/user-detail.hander.index';

const USERDETAIL_APP_PROVIDERS: Provider[] = [
  GSPUserDetailBindingData,
  GSPUserDetailForm,
  GSPUserDetailStateMachine,
  GSPUserDetailViewModel,
  GSPUserDetailUIState
];

// 
export {
  GSPUserDetailBindingData,
  GSPUserDetailForm,
  GSPUserDetailStateMachine,
  GSPUserDetailViewModel,
  GSPUserDetailUIState,

  USERDETAIL_APP_PROVIDERS,
  userDetailCommandHandlers
};



