import { SysOrgEntity } from './domain/entities/sys-org.entity';
import { Component, OnInit } from '@angular/core';
import { sysOrgsDemo } from './sys-org-json';
import { GSPSysOrgDataService } from './domain/sys-org.dataservice';

/**
 * 
 */
@Component({
  selector: 'app-sys-org-tree',
  templateUrl: './sys-org-tree.component.html',
  styleUrls: ['./sys-org-tree.component.css']
})
export class SysOrgTreeComponent implements OnInit {

  constructor (
    private sysOrgService: GSPSysOrgDataService
  ) { }
  /** */
  public sysOrgs: any[];
  /** */
  selectKeys: any[] = [];
  ngOnInit () {
    this.sysOrgService.getAllSysOrgs().subscribe(
      (result: any) => {
        this.sysOrgs = result;
        console.log(result);
      }, error => { console.log(''); }
    );
  }

  /**
   * 
   * @param event 
   */
  selectChange (event: any) {
    // 
    const curSysOrgId = event.dataItem.id;
    // 
    if (this.selectKeys == null || this.selectKeys.length === 0) {
      this.sysOrgService.getUsersWhenOrgChanged(curSysOrgId);
    } else {
      // selectedkeys
      const lastSysOrgId = this.selectKeys[0];
      // 
      if (curSysOrgId !== lastSysOrgId) {
        this.sysOrgService.getUsersWhenOrgChanged(curSysOrgId);
      }
    }
  }
}



