import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';
import { GSPUserDetailBindingData } from './gspuser-detail.bindingdata';
import { GSPUserDetailForm } from './gspuser-detail.form';
import { GSPUserDetailStateMachine } from './gspuser-detail.statemachine';
import { GSPUserDetailUIState } from './gspuser-detail.uistate';


/**
 * 
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: GSPUserDetailBindingData,
  form: GSPUserDetailForm,
  stateMachine: GSPUserDetailStateMachine,
})
export class GSPUserDetailViewModel extends ViewModel {

  public uiState: GSPUserDetailUIState;
  public stateMachine: GSPUserDetailStateMachine;

  /**
   * 
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(GSPUserDetailUIState);
  }


  /**
   * 
   */
  @NgCommand({
    name: 'userDetailFormLoad'
   })
  public formLoad() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userDetailAdd'
   })
  public add() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userDetailEdit'
   })
  public edit() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userDetailSave'
   })
  public save() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userDetailCancel'
   })
  public cancel() {}

  /**
   * 
   */
  @NgCommand({
    name: 'userDetailClose'
   })
  public close() {}
}



