import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { GSPUserPositionRepository } from './../domain/gspposition.repository';
import { GSPPositionEntity } from '../domain/gspposition.entity';

/**
 * 
 */
@Injectable()
@NgBindingData({
  entity:     GSPPositionEntity,
  repository: GSPUserPositionRepository
})
export class GSPPositionBindingData extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}




