import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { GSPUserPositionService } from '../../user-position/domain';

@Component({
  selector: 'app-pos-help',
  templateUrl: './pos-help.component.html',
  styleUrls: ['./pos-help.component.css']
})
export class PosHelpComponent implements OnInit, OnChanges {

  /** */
  @Input()
  title = '';
  /** */
  @Input()
  isShow: boolean;
  /** */
  @Input()
  initSelectedKeys: string[];

  /** */
  @Output()
  selectedAfterOk: EventEmitter<any> = new EventEmitter<any>();

  /** */
  curSelectedKeys: string[];
  selectableSettings: SelectableSettings;
  data: Object[];

  constructor (private userPosService: GSPUserPositionService) {
    // 
    this.selectableSettings = {
      checkboxOnly: true,
      mode: 'multiple',
      enabled: true
    };
  }

  ngOnInit () {
  }

  /** */
  ngOnChanges (changes: SimpleChanges): void {
    // 
    if (changes['isShow']) {
      // ,
      if (changes.isShow.firstChange) {
        // 
        this.isShow = false;
        return;
      }
      // 
      this.isShow = true;
      // 
      this.userPosService.getAllPositions()
        .subscribe(result => {
          this.data = result;
          this.curSelectedKeys = [];
          if (this.initSelectedKeys) {
            this.initSelectedKeys.map(item => this.curSelectedKeys.push(item));
          }
        }, error => {
          console.log('');
          console.log(error);
        });
    }
  }
  /** */
  btnOk () {
    this.closeDialog();
    // 
    const selectPosEntity = [];
    Observable.from(this.curSelectedKeys)
      .map(item => this.data.find(pos => pos['id'] === item))
      .subscribe(item => selectPosEntity.push(item));
    // 
    this.selectedAfterOk.next(selectPosEntity);

  }
  showDialog () {
    this.isShow = true;
  }
  closeDialog () {
    this.isShow = false;
  }
}



