import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';
import { CacheService } from '@gsp-sys/rtf-common';

/**
 * sessionwebapi
 */
@Injectable()
export class SysManagerUtil {

    /**  */
    curUserId: string;
    isView = false;

    /**webapi */
    public readonly userWebApi: string;
    /**webapi */
    public readonly userPositionWebApi: string;
    /**webapi */
    public readonly sysOrgWebApi: string;
    /**webapi */
    public readonly positionWebApi: string;
    /**webapi */
    public readonly roleWebApi: string;
    /**webapi */
    public readonly positionRoleWebApi: string;
    /**webapi */
    public readonly operationWebApi: string;

    constructor (
        private sessionCache: CacheService
    ) {
        const apiPath = '/api/runtime/sys/v1.0';
        this.userWebApi = apiPath + '/users';
        this.userPositionWebApi = apiPath + '/user-position';
        this.sysOrgWebApi = apiPath + '/sysOrgs';
        this.positionWebApi = apiPath + '/positions';
        this.roleWebApi = apiPath + '/roles';
        this.positionRoleWebApi = apiPath + '/position-role';
        this.operationWebApi = apiPath + '/funcOperations';
    }
    /**sessionId */
    private sessionId: string;
    /** funcId,*/
    public funcId: string;

    /**
     * 
     */
    getUserManageRouter (): string {
        // user-manager
        const frameWorkUrl = '/main/user-manager/' + this.funcId + '/users';
        return frameWorkUrl;
    }

    /**
     * 
     */
    getPositionManagerRouter (): string {
        // user-manager
        const frameWorkUrl = '/main/position-manager/' + this.funcId + '/positions';
        return frameWorkUrl;
    }

    getRoleManagerRouter (): string {
        const frameWorkUrl = '/main/role-manager/' + this.funcId + '/';
        return frameWorkUrl;
    }
    /**
     * Header
     */
    getHeader () {
        this.sessionId = this.getSessionId();
        const headers = new HttpHeaders().set('SessionId', this.sessionId);
        // keyheaders
        return { headers };
    }

    /**
     * sessionId
     */
    getSessionId (): string {
        return this.sessionCache.get('session');
    }

    /**Guid */
    getGuid36 (): string {
        function S4 () {
            // tslint:disable-next-line:no-bitwise
            return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        }
        return (S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4());
    }
}




