
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { GSPUserListDataService, GSPUserListRepository } from '../../domain/user-list-domain.index';
import { GSPUserListForm } from '../gspuser-list.form';
import { GSPUserListUIState } from '../gspuser-list.uistate';

/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userListSearch'
})
export class UserListSearchHandler extends CommandHandler {

  constructor (
    private dataService: GSPUserListDataService,
    private form: GSPUserListForm
  ) {
    super();
  }

  // 
  schedule () {
    this.addTask('loadData', () => {
      // 
      const userCodeOrName: string = this.form.userSearch.value;
      if (userCodeOrName == null || userCodeOrName.length === 0) {
        this.dataService.getAllUsers();
      } else {
        this.dataService.getUsersByCodeOrName(userCodeOrName);
      }
    });
  }
}



