import { Entity, NgField, EntityList, NgList } from '@farris/devkit';


/**
 * 
 */
export class GSPUserExtendEntity extends Entity {

  /**
   * 
   */
  @NgField(true)
  userId: string;

  /**
   * 
   */
  @NgField()
  email: string;

  /**
   * 
   */
  @NgField()
  mobilePhone: string;

  /**
   * 
   */
  @NgField()
  phone: string;

  /**
   * QQ
   */
  @NgField()
  qq: string;
  /**
   * WeChat
   */
  @NgField()
  weChat: string;

    /**
   * 
   */
  @NgField()
  address: string;
}



