import { GSPUserExtendEntity } from './gspuser-extend.entity';
import { Entity, NgField,  NgObject } from '@farris/devkit';


/**
 * 
 */
export class GSPUserDetailEntity extends Entity {

  /**
   * 
   */
  @NgField(
    {
      primary: true,
      dataField: 'id'
    }
  )
  id: string;

  /**
   * 
   */
  @NgField()
  code: string;

  /**
   * 
   */
  @NgField()
  name: string;

  /**
   * 
   */
  @NgField()
  sysOrgId: string;
  /**
   * 
   */
  @NgField()
  sysOrgName: string;

  /**
   * 
   */
  @NgField()
  userGroup: string;
  /**
   * 
   */
  @NgField()
  groupName: string;

  /**
   * 
   */
  @NgField()
  typeName: string;

  /**
   * 
   */
  @NgField()
  note: string;

  /**
   * 
   */
  @NgField()
  passWord: string;

  /**
   * 
   */
  @NgObject(GSPUserExtendEntity)
  userExtend: GSPUserExtendEntity;
}



