import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { GSPUserDetailStateMachine } from '../gspuser-detail.statemachine';
import { UserPasswordDataService } from '../../userpassword/domain/userpassword.dataservice';
import { SysManagerUtil } from '../../../../common/util/sys-manager-util';
import { GSPUserDetailUIState } from '../gspuser-detail.uistate';


/**
 * 
 */
@Injectable()
@NgCommandHandler({
  commandName: 'userDetailAdd'
})
export class UserDetailAddHandler extends CommandHandler {

  constructor (
    private router: Router,
    private stateMachine: GSPUserDetailStateMachine,
    private userManagerSession: SysManagerUtil,
    private pwdService: UserPasswordDataService,
    private userDetailUIState: GSPUserDetailUIState

  ) {
    super();
  }

  schedule() {
    this.addTask('add', () => {
      const addDetailUrl = this.userManagerSession.getUserManageRouter() + '/new';
      this.router.navigate([addDetailUrl, { action: 'add' }]);
      this.userDetailUIState.dataId = 'new';
    });
    // 
    this.addTask('transitState', () => {
      this.stateMachine['add']();
      this.pwdService.clearPwd();
      this.userManagerSession.isView = false;
    });
  }
}



