import { SaveUserPosition } from './handlers/user-position.save.handler';
import { DeleteUserPosition } from './handlers/user-position.delete.handler';
import { AddUserPosition } from './handlers/user-position.add.handler';
import { UserPositionFormLoadHandler } from './handlers/user-position.formload.handler';
import { Provider } from '@angular/core';
import { GSPPositionBindingData } from './user-position.bindingdata';
import { GSPUserPositionViewModel } from './user-position.viewmodel';
import { GSPUserPositionUIState } from './user-position.uistate';

const userPos_App_Providers: Provider[] = [
    GSPUserPositionUIState,
    GSPPositionBindingData,
    GSPUserPositionViewModel
];

// APP
export {
    UserPositionFormLoadHandler,
    AddUserPosition,
    DeleteUserPosition,
    SaveUserPosition,
    GSPPositionBindingData,
    GSPUserPositionViewModel,
    userPos_App_Providers
};



