import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { GSPUserDetailBindingData} from './gspuser-detail.bindingdata';

/**
 * 
 */
@Injectable()
@NgForm({
  bindingData: GSPUserDetailBindingData
})
export class GSPUserDetailForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * 
   */
  @NgFormControl({
    name: 'UserCode',
    binding: 'code'
  })
  UserCode: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserName',
    binding: 'name'
  })
  UserName: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserOrgName',
    binding: 'sysOrgName'
  })
  UserOrgName: FormControl;
  /**
   * 
   */
  @NgFormControl({
    name: 'UserGroupName',
    binding: 'userGroup'
  })
  UserGroupName: FormControl;
  /**
   * 
   */
  @NgFormControl({
    name: 'UserType',
    binding: 'typeName'
  })
  UserType: number;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserNote',
    binding: 'note'
  })
  UserNote: FormControl;

  /**
   * 
   */
  @NgFormControl(
    {
      name: 'UserPassWord',
      binding: 'passWord'
    }
  )
  UserPassWord: FormControl;

  /**
   * --------------------------------------------------------------------
   * Tab
    * --------------------------------------------------------------------
   */
  /**
   * 
   */
  @NgFormControl({
    name: 'UserEmail',
    binding: 'userExtend.email'
  })
  UserEmail: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserPhone',
    binding: 'userExtend.phone'
  })
  UserPhone: FormControl;


  /**
   * 
   */
  @NgFormControl({
    name: 'UserMobilePhone',
    binding: 'userExtend.mobilePhone'
  })
  UserMobilePhone: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserWeChat',
    binding: 'userExtend.weChat'
  })
  UserWeChat: FormControl;
  /**
   * QQ
   */
  @NgFormControl({
    name: 'UserQQ',
    binding: 'userExtend.qq'
  })
  UserQQ: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'UserAddress',
    binding: 'userExtend.address'
  })
  UserAddress: FormControl;
}




