import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { UserPasswordDataService } from '../../userpassword/domain/userpassword.dataservice';
import { SysManagerUtil } from '../../../../common/util/sys-manager-util';

@Injectable()
@NgCommandHandler({
  commandName: 'userDetailClose'
})
export class UserDetailCloseHandler extends CommandHandler {

  constructor (
    private router: Router,
    private userManager: SysManagerUtil,
    private pwdService: UserPasswordDataService
  ) {
    super();
  }

  /**
   * 
   */
  schedule() {
    this.addTask('close', () => {
      const userListPath = this.userManager.getUserManageRouter();
      this.router.navigateByUrl(userListPath);
      this.pwdService.clearPwd();
    });
  }
}



